.product-card {
    position: relative;
    width: 100%;
    height: 100%;
    min-height: 150px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    border: 1px solid var(--gray-regular);
    border-radius: 8px;

    &-img {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 0 4px;
        margin-bottom: 8px;

        &-picture {
            width: 100%;
            height: 68px;
            border-radius: 4px;
        }
    }

    &-sale {
        position: absolute;
        top: 8px;
        right: 8px;
        font-weight: 700;
        font-size: 0.75rem;
        line-height: 1.25rem;
        color: var(--white);
        padding: 1.5px 2.5px 0.5px;
        border-radius: 18px;
        &-yollow {
            background-color: var(--yollow);
        }

        &-blue {
            background-color: var(--aqua);
        }
    }

    &-content {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: space-between;
        min-height: 70px;
        padding: 0 7px 7px 7px;
    }

    &-information {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        margin-bottom: 4px;
        &-item {
            display: flex;
            align-items: center;
            &:not(:last-child) {
                margin-right: 9px;
            }
            &-svg {
                display: flex;
                align-items: center;
                justify-content: center;
                margin-right: 4px;
            }
        }
    }
    &-bottom {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        margin-top: 7px;
    }

    &-price {
        font-size: 1.25rem;
        line-height: 1.5rem;
        color: var(--text-color);
        font-weight: 500;
    }
    &-btn {
        width: 20px;
        height: 20px;
        border-radius: 50%;
        background-repeat: no-repeat;
        &-wrapper {
          position: relative;
        }
        &-red {
          background-color: var(--red);
          background-image: url(../img/shopping-bag-02.svg);
          background-position: 6px 5.5px;
        }
        &-yollow {
            position: absolute;
            top: 0;
            left: 0;
            transform-origin: top;
            transform: scale(0);
            background-color: var(--yollow);
            background-image: url(../img/trash-01.svg);
            background-position: 6px 6px;
            z-index: 2;
            transition: transform .3s ease-in-out;
        }

        &-wrapper.active-yollow &-yollow{
          transform: scale(1);
        }
    }
}
